@use 'base/variables' as v;
@use 'base/mixins' as m;


.barra-mobile {
    background-color: v.$indigo;
    padding: 2rem 4rem;
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include m.tablet {
        display: none;
    }

    

    h1{
        color: v.$blanco;
        margin: 0;
    }

    .menu{
        img{
            width: 0.8rem;
            &:hover{
                cursor: pointer;
            }
        }
    }

    
}



.barra{
    display: none;
   
    @include m.tablet{
        display: flex;
        justify-content: space-between;
        align-items: center;
        background-color: v.$blanco;
        padding: 1rem 1rem;
        box-shadow:  1px 12px 9px -13px rgba(0,0,0,0.56);
    
        
    }
    
    p{
        margin: 0;
        font-size: 1.7rem;

        span{
            color: v.$indigo;
            font-weight: 700;
        }
    }

    .cerrar-sesion {
        @include m.boton(darken(v.$indigo,30%), v.$blanco);
        padding: 1rem;
        font-size: 1.2rem;
        border-radius: 0.5rem;
    }
    
}
