@use 'base/variables' as v;
@use 'base/mixins' as m;

.sidebar{
    opacity: 0;
    background-color: v.$indigo;
    height: 0;
    overflow: hidden;
    transition-property: opacity;
    transition-duration: 500ms;

    
    &.mostrar{
        padding: 2rem;
        opacity: 1;
        height: 100%;
        position: absolute;
        top: 0;
        right: 0;
        left: 0;
        bottom: 0;
        background-color: rgb(0 0 0 /0.90);
        @include m.tablet{
            display: none;
        }
        
    }
    &.ocultar{
        opacity: 0;
    }

    @include m.tablet{
        height: 100%;
        opacity: 1;
        padding: 2rem;
        background-color: v.$indigo;
       
    }

    .contenedor-sidebar{
        display: flex;
        justify-content: space-between;
        align-items: center;
        
        h2{
            margin: 0;
                color: v.$blanco;
           
        }
        .cerrar-menu{
            display: flex;
            justify-content: center;
            height: 2rem;
            width: 2rem;
            img{
                &:hover{
                    cursor: pointer;
                }
            }
            @include m.tablet {
                display: none;
            }

        }
    }
    
    .sidebar-nav{
        padding-top: 5rem;
        
        a{
            color: v.$blanco;
            display: block;
            margin-top: 2rem;
            padding: 0.7rem;
            transition-property: background-color;
            transition-duration: 300ms;
            font-size: 2.8rem;
            text-transform: uppercase;
            margin-bottom: 2rem;
            text-align: center;

            &:hover,
            &.activo {
                background-color: darken(v.$indigo, 20%);
                border-radius: 1rem;
            }

            @include m.tablet{
                font-size: 1.7rem;
                margin-bottom: 2rem;
                text-align: left;
            }

        }
            

    }
    .cerrar-sesion-mobile{
        margin-top: 10rem;

        .cerrar-sesion{
            @include m.boton(v.$indigo, v.$blanco);
        }
            
        @include m.tablet{
            display: none;
        }
    }
    
}
    