@use 'base/variables' as v;
@use 'base/mixins' as m;

.contenedor-nueva-tarea{
    display: flex;
    justify-content: center;
    
    .agregar-tarea {
        @include m.boton(v.$cyan, v.$blanco);

    }
    
}

.listado-tareas {
    list-style: none;
    padding: 0;
    margin-top: 2rem;

    .no-tareas {
        text-align: center;
        
    }
}

.tarea {
    padding: 0.5rem 2rem;
    border-bottom: 2px solid darken(v.$grisClaro, 5%);
    margin-bottom: 2rem;
    display: flex;
    justify-content: space-between;
    align-items: center;

    &:last-of-type{
        border-bottom: none;
    }
}

.opciones{
    display: flex;
    gap: 1rem;

    button{
        border: none;
        font-size: 1.4rem;
        padding: 0.5rem 1rem;
        font-weight: 700;
        text-transform: uppercase;
        border-radius: 1rem;
        &:hover {
            cursor: pointer;
        }
    }
    .pendiente {
        background-color: v.$naranja;
        color:v.$blanco;
    }
    .completada {
        background-color: v.$azul;
        color: v.$blanco;

    }
    .eliminar-tarea {
        background-color: v.$rojo;
        color: v.$blanco;
    }
}