@use 'base/variables' as v;
@use 'base/mixins' as m;

.no-proyecto{
    padding-top: 3rem;
    display: block;
    text-align: center;
       
        a{
            color: v.$indigo;
            @include m.boton(v.$indigo, v.$blanco);
        }
}

.listado-proyectos{
    list-style: none;
    padding: 0;
    margin: 3rem;
    
    @include m.grid(1,2rem);
    @include m.tablet{
        @include m.grid(2, 2rem);
    }
    @include m.desktop {
        @include m.grid(3, 2rem);
    }
    
    .proyecto {
        background-color: lighten(v.$indigo, 5%);
        min-height: 12rem;
        display: flex;
        align-items: center;
        padding: 2rem;
        line-height: 2;
        border-radius: 0.5rem;


        &:hover {
            background-color: darken(v.$indigo, 5%);
        }

        a{
            color: v.$blanco;

            
        }
       
    }

}